import React, { createContext, useContext, useCallback, useState } from 'react';
import { logger } from '../utils/logger';
const MediaBinContext = createContext(undefined);
export const MediaBinProvider = ({ children }) => {
    const [items, setItems] = useState([]);
    const [selectedItem, setSelectedItem] = useState(null);
    const addItems = useCallback((newItems) => {
        logger.debug('Adding media items:', newItems);
        setItems(current => {
            const updatedItems = [...current, ...newItems];
            // Sync with timeline state
            if (window.timelineDispatch) {
                window.timelineDispatch({
                    type: 'SET_STATE',
                    payload: {
                        ...window.timelineState,
                        mediaBin: {
                            ...(window.timelineState?.mediaBin || {}),
                            items: updatedItems
                        }
                    }
                });
            }
            return updatedItems;
        });
    }, []);
    const removeItem = useCallback((id) => {
        logger.debug('Removing media item:', id);
        setItems(current => current.filter(item => item.id !== id));
        setSelectedItem(current => current?.id === id ? null : current);
    }, []);
    const selectItem = useCallback((item) => {
        logger.debug('Selecting media item:', item);
        setSelectedItem(item);
    }, []);
    const value = {
        items,
        selectedItem,
        addItems,
        removeItem,
        selectItem
    };
    // Always expose context for testing
    React.useEffect(() => {
        window.mediaBinContext = value;
    }, [value]);
    return (React.createElement(MediaBinContext.Provider, { value: value }, children));
};
export const useMediaBin = () => {
    const context = useContext(MediaBinContext);
    if (!context) {
        throw new Error('useMediaBin must be used within a MediaBinProvider');
    }
    return context;
};
